<template>
    <div class="container">
        <h1>{{ $t("title.linearEquationsSystem") }}</h1>
        <p class="lead">
            {{ $t("description.linearEquationsSystem") }}
        </p>
        <div class="row">
            <div class="col-12 col-sm-3">
                <label>{{ $t("text.variableNumber")}}</label>
                <select class="custom-select" v-model="leNumber" @change="generateInputArray">
                    <option value="2" selected>2</option>
                    <option v-for="i in 97" :value="i + 2">{{ i + 2}}</option>
                </select>
            </div>
            <div class="col-12 col-sm-12 col-md-6 mb-3">
                <label>{{ $t("text.dataInputType") }}</label>
                <div class="input-group">
                    <div class="btn-group btn-group-toggle"
                         style="overflow-x: auto">
                        <label class="btn btn-outline-info" :class="{active: inputType === 'single'}">
                            <input type="radio" name="type" autocomplete="off" value="single" v-model="inputType"
                                   checked>{{ $t('text.artificialInput') }}
                        </label>
                        <label class="btn btn-outline-info" :class="{active: inputType === 'text'}">
                            <input type="radio" name="type" autocomplete="off" value="text" v-model="inputType"
                                   checked>{{ $t('text.textInput') }}
                        </label>
                        <label class="btn btn-outline-info" :class="{active: inputType === 'file'}">
                            <input type="radio" name="type" autocomplete="off" value="file" v-model="inputType"
                                   checked>{{ $t('text.fromFileImport') }}
                        </label>
                        <label class="btn btn-outline-info" :class="{active: inputType === 'jsonFile'}">
                            <input type="radio" name="type" autocomplete="off" value="jsonFile" v-model="inputType"
                                   checked>{{ $t('text.fromJsonImport') }}
                        </label>
                    </div>
                </div>
            </div>
            <div class="col-12">
                <hr>
            </div>
            <div class="col-12">
                <h5>{{ $t("text.dataInput") }}:</h5>
                <p>
                    𝑎<sub>0,0</sub><b>𝑥</b><sub>0</sub> + 𝑎<sub>0,1</sub><b>𝑥</b><sub>1</sub> + ... = 𝘣<sub>0</sub>
                </p>
            </div>
            <div v-if="inputType === 'single' && status === 'edit'" v-for="(iValue, ikey) in leInputA" class="col-12 mb-2">
                <div class="row">
                    <div class="col-12">
                        <label>{{$t('text.equation') }} {{ ikey + 1 }} :</label>
                    </div>
                    <div v-for="(jValue, jkey) in iValue" class="col-6 col-sm-4 col-md-3 col-lg-2 mb-1">
                        <div class="input-group">
                            <div class="input-group-prepend">
                                <span class="input-group-text" :id="'le-' + ikey + '-' + jkey">𝑎<sub>{{ ikey }},{{ jkey }}</sub></span>
                            </div>
                            <input type="number" class="form-control" :aria-describedby="'le-' + ikey + '-' + jkey"
                                   v-model="leInputA[ikey][jkey]">
                        </div>
                    </div>
                    <div class="col-6 col-sm-4 col-md-3 col-lg-2 mb-1">
                        <div class="input-group">
                            <div class="input-group-prepend">
                                <span class="input-group-text text-danger"
                                      :id="'le-' + ikey">𝘣<sub>{{ ikey }}</sub></span>
                            </div>
                            <input type="number" class="form-control" :aria-describedby="'le-' + ikey"
                                   v-model="leInputB[ikey]">
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="inputType === 'single' && status === 'show'" v-for="(iValue, ikey) in leInputA" class="col-12 mb-2">
                <div class="row">
                    <div class="col-12">
                        <label>{{$t('text.equation') }} {{ ikey + 1 }}: </label>
                        <p v-html="showLe(ikey)"></p>
                    </div>
                </div>
            </div>
            <div v-if="inputType === 'single'" class="col-12">
                <div class="btn-toolbar" role="toolbar">
                    <button v-if="status === 'edit'" class="btn btn-primary" @click="calculate">{{ $t('text.calculate') }}</button>
                    <button v-if="status === 'show'" class="btn btn-info" @click="status = 'edit'">{{ $t('text.edit') }}</button>
                    <div class="dropdown ml-2">
                        <button class="btn btn-secondary dropdown-toggle" type="button" id="exportMenuButton"
                                data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            {{ $t('text.export') }}
                        </button>
                        <div class="dropdown-menu" aria-labelledby="exportMenuButton">
                            <a class="dropdown-item" href="#" @click="exportData">{{ $t("text.plainText") }}</a>
                            <a class="dropdown-item" href="#" @click="exportJsonData">JSON</a>
                        </div>
                    </div>
                </div>

            </div>
            <div v-if="inputType === 'text'" class="col-12 mb-2">
                <div class="form-group">
                    <textarea class="form-control" rows="7"
                              aria-describedby="personTextInputSmail"
                              placeholder="3 4 5"
                              v-model="textInput"></textarea>
                    <small id="personTextInputSmail" class="form-text text-muted">
                        {{ $t('message.oneDataOneLineEquation') }}
                    </small>
                </div>
                <button type="button" class="btn btn-danger" @click="parseText">{{
                    $t('text.input') }}
                </button>
            </div>
            <div v-if="inputType === 'jsonFile'" class="col-12 mb-2">
                <div class="form-group">
                    <input type="file" class="form-control-file" @change="event => jsonFile = event.target.files[0]">
                </div>
                <button type="button" class="btn btn-danger" @click="importJson">{{
                    $t('text.input')}}
                </button>
            </div>
            <div v-if="inputType === 'file'" class="col-12 mb-2">
                <div class="form-group">
                    <input type="file" class="form-control-file" @change="event => textFile = event.target.files[0]">
                </div>
                <button type="button" class="btn btn-danger" @click="importText">{{
                    $t('text.input')}}
                </button>
            </div>
            <div class="col-12">
                <hr>
            </div>
            <div class="col-12">
                <h5>{{ $t('text.solution') }}:</h5>
            </div>
            <div class="col-12 mb-2">
                <div class="row">
                    <div v-for="i, key in leAns " class="col-12">
                        𝑥<sub>{{ key }}</sub> = {{ i }}
                    </div>
                </div>
            </div>
            <div class="col-12">
                <hr>
            </div>
            <div class="col-12">
                <h5>{{ $t('text.checkCalculation') }}:</h5>
                <p>
                    𝑎<sub>0,0</sub>𝑥<sub>0</sub> + 𝑎<sub>0,1</sub>𝑥<sub>1</sub> + ... = 𝘣
                </p>
            </div>
            <div class="col-12 mb-2">
                <div class="row">
                    <div v-for="(value, index) in leInputCheckA" class="col-6 col-sm-4 col-md-3 col-lg-2 mb-1">
                        <div class="input-group">
                            <div class="input-group-prepend">
                                <span class="input-group-text" :id="'le-check' + index">𝑎<sub>{{ index }}</sub></span>
                            </div>
                            <input type="number" class="form-control" :aria-describedby="'le-check' + index"
                                   v-model="leInputCheckA[index]">
                        </div>
                    </div>
                    <div class="col-6 col-sm-4 col-md-3 col-lg-2 mb-1">
                        <InputHasCopy :value="checkAns">
                            <div class="input-group-prepend">
                                <span class="input-group-text text-danger">𝘣</span>
                            </div>
                        </InputHasCopy>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import * as math from 'mathjs'
    import FileSaver from 'file-saver';
    import InputHasCopy from "../../components/elements/InputHasCopy";

    export default {
        metaInfo() {
            return {
                title: this.$i18n.t('title.linearEquationsSystem'),
                meta: []
            }
        },
        data() {
            return {
                inputType: "single",
                textInput: "",
                leNumber: "2",
                leInputA: [],
                leInputCheckA: [],
                leInputB: [],
                leAns: [],
                textFile: null,
                jsonFile: null,
                status: "edit"
            }
        },
        methods: {
            generateInputArray() {
                this.leInputA = Array(parseInt(this.leNumber));
                this.leInputB = Array(parseInt(this.leNumber));
                this.leInputCheckA = Array(parseInt(this.leNumber));
                this.leAns = Array(parseInt(this.leNumber));

                for (let i = 0; i < this.leInputA.length; i++) {
                    this.leInputA[i] = new Array(parseInt(this.leNumber));
                    this.leAns[i] = 0;
                    this.leInputCheckA[i] = 0;
                }

                this.status = 'edit'
            },
            parseText() {
                let leInputA = [];
                let leInputB = [];

                this.textInput.split("\n").forEach((value) => {
                    let row = value.split(" ");
                    if (row.length === parseInt(this.leNumber) + 1) {
                        leInputB.push(row.pop());
                        leInputA.push(row);
                    }
                });

                this.leInputA = leInputA;
                this.leInputB = leInputB;
                this.inputType = 'single';
            },
            importText() {
                let fileReader = new FileReader();
                let self = this;
                fileReader.onload = event => {
                    this.textInput = fileReader.result;

                    self.parseText();
                };

                fileReader.readAsText(this.textFile);
            },
            importJson() {
                let fileReader = new FileReader();
                let self = this;
                fileReader.onload = event => {
                    let json  = JSON.parse(fileReader.result);

                    self.leInputA = json.A;
                    self.leInputB = json.B;

                    self.inputType = "single";
                };

                fileReader.readAsText(this.jsonFile)
            },
            calculate() {
                let d = math.det(this.leInputA);
                let ans = Array(parseInt(this.leNumber));

                for (let i = 0; i < this.leAns.length; i++) {
                    let dx = JSON.parse(JSON.stringify(this.leInputA));
                    dx.map((e, index) => {
                        e[i] = this.leInputB[index];

                        return e;
                    });

                    ans[i] = math.det(dx) / d;
                }

                this.leAns = ans;
                this.status = 'show';
            },
            exportData() {
                let context = this.leInputA.reduce((previousValue, currentValue, currentIndex) => {
                    return previousValue + currentValue.join(" ") + " " + this.leInputB[currentIndex] + "\n";
                }, "");

                const file = new File([context], "LinearEquationsSystem.txt", {type: "text/plain;charset=utf-8"});
                FileSaver.saveAs(file);
            },
            exportJsonData() {
                let context = JSON.stringify({
                    A: this.leInputA,
                    B: this.leInputB,
                });
                const file = new File([context], "LinearEquationsSystem.txt", {type: "text/plain;charset=utf-8"});
                FileSaver.saveAs(file);
            },
            showLe(index) {
                let context = this.leInputA[index].reduce((previousValue, currentValue, currentIndex) => {
                    return previousValue + `${parseInt(currentValue)}<b>𝑥</b><sub>${currentIndex}</sub>` + (currentIndex === this.leInputA.length - 1 ? " = " : " + ")
                }, "");

                context += this.leInputB[index];

                return context;
            }
        },
        computed: {
            checkAns() {
                return this.leInputCheckA.reduce(((previousValue, currentValue, currentIndex) => previousValue + currentValue * this.leAns[currentIndex]), 0)
            }
        },
        watch: {},
        components: {
            InputHasCopy
        },
        mounted() {
            this.generateInputArray();
        }
    }
</script>

